import axios from '../plugins/axios'

async function getUserTree() {
    return  await axios.get('/api/v1/pro100r/usertree')
}


async function getTasks() {
    return await axios.get('/api/v1/pro100r/card')
}
async function getTask(id) {
    return  await axios.get('/api/v1/pro100r/card/'+id)
}

async function addTask(task) {
    return await axios.post('/api/v1/pro100r/card', {
        forUserIds: task.forUserIds,
        validUntil: task.validUntil,
        header: task.header,
        priority: task.priority,
        reaction: task.reaction,
        note: task.note,
        file: task.file,
        color: task.color,
        isCommentingAllowed: task.isCommentingAllowed,
        cardType: task.cardType,
        cardState: task.cardState
    })
}
async function updateTask(task){
    return await axios.post('/api/v1/pro100r/card/'+task.id,{
        forUserIds: task.forUserIds,
        validUntil: task.validUntil,
        header: task.header,
        priority: task.priority,
        reaction: task.reaction,
        note: task.note,
        file: task.file,
        color: task.color,
        isCommentingAllowed: task.isCommentingAllowed,
        cardType: task.cardType,
        cardState: task.cardState
    })
}
async  function getCommentsTask(taskId){
    return  await axios.get(`/api/v1/pro100r/cardmessage/${taskId}`)
}
async function addCommentTask(comment) {
    return await axios.post(`/api/v1/pro100r/cardmessage/${comment.idTask}`, {
        message: comment.message
    })
}

async function getCardTypes() {
    return await axios.get('/api/v1/pro100r/enums/cardtypes')
}
async function getCardStateTypes() {
    return await axios.get('/api/v1/pro100r/enums/cardstatetypes')
}
async function getCardCommentTypes() {
    return await axios.get('/api/v1/pro100r/enums/cardcommenttypes')
}

async function getHistoryTask(taskId) {
    return await axios.get('/api/v1/pro100r/card/history/'+taskId)
}
export default {
    getUserTree,
    getTasks,
    addTask,
    getCommentsTask,
    addCommentTask,
    getCardCommentTypes,
    getCardStateTypes,
    getCardTypes,
    getTask,
    getHistoryTask,
    updateTask
}
