import axios from '../plugins/axios'


async function login(user) {
    try{
        return await axios.post('/api/v1/pro100r/account/login',{
            username: user.login,
            password: user.password
        })
    }catch (e) {
        throw e
    }
}
async function logout(){
    try{
        return await axios.post('api/v1/pro100r/account/logout')
    }catch (e) {
        throw e
    }
}
async function refresh(){
    try {
        return await axios.get('/api/v1/pro100r/account/refresh')
    }catch (e) {
        throw e
    }
}
async function getInfoUserAndRole() {
    try {
        return await axios.get('api/v1/pro100r/account/info')
    }catch (e) {
        throw e
    }
}

export default {
    login,
    logout,
    getInfoUserAndRole,
    refresh,
}
