<template>
    <v-row dense>
        <v-col cols="11">
            <v-textarea
                    v-if="typeInput=='textarea'"
                    v-model="text"
                    dense
                    outlined
                    :label="title"/>
            <v-text-field v-if="typeInput=='mainInput'"
                          v-model="text"
                          dense
                          regular
                          :label="title" style="font-size: 1.3rem"/>
            <v-text-field v-if="typeInput=='input'"
                          v-model="text"
                          dense
                          outlined
                          :label="title"/>
        </v-col>
        <v-col>
            <v-btn
                    dense
                    @click.stop="toggle ? endSpeechRecognition() : startSpeechRecognition()"
                    icon
                    :color="!toggle ? 'grey' : (speaking ? 'red' : 'red darken-3')"
                    :class="{'animated infinite pulse': toggle}"
            >
                <v-icon>{{toggle ? 'mic_off' : 'mic'}}</v-icon>
            </v-btn>
        </v-col>
    </v-row>
</template>

<script>
    let SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition
    let recognition = SpeechRecognition? new SpeechRecognition() : false
    export default {
        name: "SpeechFileInput",
        props: {
            lang: {
                type: String,
                default: 'ru-RU'
            },
            text: {
                type: [String, null],
                required: true
            },
            title: String,
            typeInput: {
                type: String,
                default: 'input'
            }
        },
        data () {
            return {
                error: false,
                speaking: false,
                toggle: false,
                runtimeTranscription: '',
                sentences: []
            }
        },
        methods: {
            checkCompatibility () {
                if (!recognition) {
                    this.error = 'Распознавание речи недоступно в этом браузере. Пожалуйста, используйте Chrome или Firefox'
                }
            },
            endSpeechRecognition () {
                recognition.stop()
                this.toggle = false
                this.$emit('speechend', {
                    sentences: this.sentences,
                    text: this.sentences.join('. ')
                })
            },
            startSpeechRecognition () {
                if (!recognition) {
                    this.error = 'Распознавание речи недоступно в этом браузере. Пожалуйста, используйте Chrome или Firefox'
                    return false
                }
                this.toggle = true
                recognition.lang = this.lang
                recognition.interimResults = true

                recognition.addEventListener('speechstart', event => {
                    this.speaking = true
                })

                recognition.addEventListener('speechend', event => {
                    this.speaking = false
                })

                recognition.addEventListener('result', event => {
                    const text = Array.from(event.results).map(result => result[0]).map(result => result.transcript).join('')
                    this.runtimeTranscription = text
                })

                recognition.addEventListener('end', () => {
                    if (this.runtimeTranscription !== '') {
                        this.sentences.push(this.capitalizeFirstLetter(this.runtimeTranscription))
                        this.$emit('update:text', `${this.text}${this.sentences.slice(-1)[0]}. `)
                    }
                    this.runtimeTranscription = ''
                    recognition.stop()
                    if (this.toggle) {
                        // keep it going.
                        recognition.start()
                    }
                })
                recognition.start()
            },
            capitalizeFirstLetter (string) {
                return string.charAt(0).toUpperCase() + string.slice(1)
            }
        },
        mounted () {
            this.checkCompatibility()
        }
    }
</script>

