<script>
    import CreateTask from "./Task/CreateTask";
    import ProgressTask from "./InfoTask/ProgressTask";
    import {mapActions, mapState} from 'vuex'
    export default {
        name: "Tasks",
        components: {ProgressTask, CreateTask},
        data: () => ({
            dialog: false,
            expanded: [],
            singleExpand: false,
        }),
        computed:{
            ...mapState('tasks', {
                tasks: 'tasks',
            })
        },
        methods:{
          ...mapActions('tasks', {
              getTasks: 'get_tasks',
              getHistory: 'get_history_task'
          }),
            loadHistoryTask(id){
                this.getHistory(id)
            }
        },
        created() {
            this.getTasks()
        }
    }
</script>

