import APiServices from "../../services/APiServices";

const state = {
    userTree: [],
    tasks: [],
    taskType: [],
    taskState: [],
    historyTask: []
}

const getters = {

}

const  actions = {
    async get_user_tree({commit}){
        let user_tree = await APiServices.getUserTree()
        commit('SAVE_USER_TREE', user_tree.data.value)
    },
    async get_tasks({commit}){
        let tasks = await APiServices.getTasks()
        commit('SAVE_TASKS', tasks.data.value)
    },
    async get_task_type({commit}){
        let tasktype = await APiServices.getCardTypes()
        commit('SAVE_TASK_TYPE', tasktype.data.value)
    },
    async add_task({commit}, task){
        let addtask = await APiServices.addTask(task)
        commit('ADD_TASK', addtask.data.value)
    },
    async update_task({commit}, task){
        let updateTask = await APiServices.updateTask(task)
        commit('UPDATE_TASK', updateTask.data.value)
    },
    async add_comment_task(comment){
        let add_comment_task = await APiServices.addCommentTask(comment)
    },
    async get_task_state({commit}){
        let taskState = await APiServices.getCardStateTypes()
        commit('SAVE_TASK_STATE', taskState.data.value)
    },
    async get_history_task({commit}, taskId){
        let historyTask = await APiServices.getHistoryTask(taskId)
        commit('SAVE_HISTORY_TASK', historyTask.data.value)
    }
}

const mutations = {
    SAVE_USER_TREE(state, userTree){
        state.userTree = userTree
    },
    SAVE_TASKS(state, tasks){
        state.tasks = tasks
    },
    SAVE_TASK_TYPE(state, taskType){
        state.taskType = taskType
    },
    ADD_TASK(state, addTask){
        state.tasks = addTasks
    },
    UPDATE_TASK(state, payload){

    },
    SAVE_TASK_STATE(state, taskState){
        state.taskState = taskState
    },
    SAVE_HISTORY_TASK(state, history){
        state.historyTask = history
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}
